.email-form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;

    margin-top: 2em;
}

.email-form-input {
    margin: 1em 0 1em 0;
    padding: 1em;
    background: rgba(155, 155, 155, 0.1);
    
    min-width: 200px;
    max-width: 500px;

    border: 1em solid white; 
    border: 0.1em solid rgba(155, 155, 155, 0.774);
    border-radius: 0.5em;

    font-family: 'Merriweather ', sans-serif;
    font-size: 1em;
}
  
input:focus {
    outline: 0;
}



